.x-landmark {
    display: none;
}


// RESET PRO THEME BUTTON STYLES
.x-btn {
    text-shadow: none;
    &:hover {
        text-shadow: none;
    }
}
.button,
[type="submit"] {
    @extend .x-btn;
}

// RESET WP CONACT FORM 7
input[type='submit'].wpcf7-submit {
    font-weight: 700;
    letter-spacing: .01;
    border: 5px solid var(--color-secondary);
    &:hover {
        box-shadow: 2px 2px 2px rgba(0, 0, 0, .35);
        transform: translateY(-3px);
    }
}

.wpcf7-not-valid-tip {
    color: var(--color-secondary);
}